.welcome_container{
    display: flex;
    align-items: center;

    .user-space{
        display: flex;
        flex-direction: column;
        align-items: center;
        
        .user{
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        .user-display{
            text-align: center;
            display: none;
            transition: all .5 ease-in-out;
        }

        .user-display{
            display: block;
            top: 80px;
        }
    }

    .logout-btn{
        border: none;
        color: blue;
        background: none;
    }
}

.login_container{
    display: flex;
    justify-content: center;
    width: 80%;
    margin: auto;

    .login{
        text-align: center;
        width: 100%;
        margin: auto;

        .error_space{
            .error_alert{
                font-size: 14px;
                margin: 4px 0;
                color: red;
            }
        }

        .login_inputs{
            display: flex;
            flex-direction: column;

            #entrar{
                margin: 2px 0;
                font-size: 22px;
            }

            input{
                margin: 6px 0;
                padding: 6px 12px;
                border: 1px solid black;
                border-radius: 15px;
            }

            input:focus{
                background-color: rgba(166, 249, 255, 0.4);
            }

            #noacc{
                margin: 40px 0 4px 0;
                font-size: 16px;
            }

            .btn_entrar{
                width: 40%;
                margin: 5px auto 0 auto;
                border: 1px solid black;
                border-radius: 12px;
                padding: 5px 0;
                background-color: rgb(138, 255, 144);
                border: 1px solid rgb(13, 255, 25);

            }

            .btn_entrar:hover{
                cursor: pointer;
                background-color: rgb(173, 255, 92);
            }

            .btn_rgster{
                width: 50%;
                margin: auto;
                border: none;
                background-color: rgba(250, 245, 203,.1);
                color: blue;
                text-decoration: underline;
            }

            .btn_rgster:hover{
                cursor: pointer;
            }

            .btn-log{
                display: none;
            }
    
            .btn-log.showBtn{
                display: block;
            }
    
            .login_inputs{
                display: none;
                flex-direction: column;
            }
            .login_inputs.showlogmenu{
                display: block;
            }
        }
    }
}

.registration_container{
    .registration{
        text-align: center;
        width: 200px;

        .regster{
            margin: 0 0 10px 0;
            font-size: 22px;
        }

        .registration_inputs{
            display: flex;
            flex-direction: column;
            align-content: center;
            align-items: center;

            label{
                font-size: 15px;
                align-self: start;
                align-self: center;
                margin: 0 0 2px 0;
            }

            input{
                margin: 0 auto 9px auto;
                padding: 5px 10px;
                border: 1px solid black;
                border-radius: 15px;
            }
            input:focus{
                background-color: rgb(245, 255, 234);
                box-shadow: 0 0 4px rgb(160, 255, 160);
            }
        }

        .alrgth{
            margin: 25px auto 6px auto;
            font-size: 16px;
        }

        .btn_login{
            width: 90%;
            font-weight: 600;
            margin: 7px auto 0 auto;
            border: 1px solid black;
            color: rgb(255, 255, 255);
            font-size: 15px;
            border-radius: 12px;
            padding: 3px 0;
            background-color: rgb(92, 116, 255);
        }

        .btn_login:hover{
            cursor: pointer;
            background-color: rgb(76, 103, 253);
        }

        .btn_lgns{
            border: none;
            color: blue;
            text-decoration: underline;
            background-color: rgba(250, 245, 203,.1);
        }

        .btn_lgns:hover{
            cursor: pointer;
        }
    }
}